import React from "react";
import { Link } from "react-router-dom";

function Header() {
     return (
          <div className="h-16 w-full top-0 flex flex-row items-center justify-between lg:px-40 pt-5 z-20 absolute">
               <Link to="/" className="flex flex-row items-end">
                    <img src="../images/2.png" alt="" className="w-16" />
                    <p className="text-4xl font-bold ml-2 bg-gradient-to-r from-purple-500 to-indigo-400 text-transparent bg-clip-text">Avchain</p>
               </Link>
               <div className="flex flex-row items-center justify-center space-x-20">
                    <p>Buy</p>
                    <p>Sell</p>
                    <p className="bg-violet-900 p-2 px-4 rounded-md hover:bg-violet-700 cursor-pointer">Contact</p>
               </div>
          </div>
     );
}

export default Header;
