import React from "react";
import { useParams } from "react-router-dom";
import { formatCash } from "./functions";
import { coinData } from "./coinData";
import { useState } from "react";
import Fade from "react-reveal/Fade";
import { Link } from "react-router-dom";

function CoinPage() {
     const { title } = useParams();
     return (
          <div className="bg-gradient-to-b from-[#07071c] to-black">
               <div className="pt-24 py-10 px-40 h-screen">
                    {coinData
                         .filter((coin) => coin.symbol === title)
                         .map((coin, index) => (
                              <Fade>
                                   <div key={index}>
                                        <div className="flex flex-row items-center">
                                             <img src={coin.logo_url} alt="" className="w-12 rounded-3xl" />
                                             <h1 className="text-5xl font-semibold ml-2">{coin.name}</h1>
                                             <h1 className="ml-2 mt-3 bg-gray-700 p-1 px-2 rounded-xl">{coin.symbol}</h1>
                                        </div>
                                        <p className="mt-1  p-1 px-2 rounded-xl text-sm">Rank #{coin.rank}</p>
                                        <p className="text-2xl mt-4">${parseFloat(coin.price).toLocaleString("en-US", { maximumFractionDigits: 2 })}</p>
                                        {coin.max_supply ? <p>Max Supply - {parseFloat(coin.max_supply).toLocaleString("en-US", { maximumFractionDigits: 2 })}</p> : null}
                                   </div>
                              </Fade>
                         ))}
               </div>
          </div>
     );
}

export default CoinPage;
